body {
  box-sizing: border-box;
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  place-items: center;
  gap: 40px 30px;
  min-width: 500px;
  padding: 35px;
  display: grid;
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    place-self: center;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    place-self: center;
  }
}

@media (width >= 1500px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    place-self: center;
  }
}

